<template>
<div 
    class='page'>
    <swiper
        ref='details'
        v-if='show.detail'
        class="swiper events-detail"
        :options="swiperOption">
        <swiper-slide
            :style="{width:px(522),height:px(750),top:tpx(476,1800),borderRadius:px(10)}"
            :key='`detail-${index}`'
            v-for='(event,index) in eventList'>
            <div>
                <div 
                    @click.stop="show.detail = false"
                    :style="{width:px(68),height:px(68)}"
                    style="position:absolute;right:0;top:0;"></div>
                <div
                    :style="{left:px(66),height:px(54),lineHeight:px(54),width:px(148),top:px(54)}"
                    style="position:absolute;right:0;top:0;text-align:center;color:#FFF;font-weight:100;"><i>{{event.event_month}}月{{event.event_day}}日</i></div>
                <div 
                    @click.stop="selectEvent(index,event,1)"
                    :style="{bottom:px(45),height:px(94)}"
                    style="position:absolute;width:100%;"></div>                    
                <div
                    :style="{top:px(130),paddingLeft:px(64),paddingRight:px(64)}"
                    style="color:#FFF;text-align:left;position:absolute;">
                    <div>{{event.event_name}}</div>
                    <div
                        style="font-weight:100;word-break: break-all;"
                        :style='{marginTop:px(50)}'>{{event.related_answer_essence}}</div>
                </div>
                <div
                    :style="{bottom:px(180),paddingLeft:px(64),paddingRight:px(64)}"
                    style="color:rgba(255,255,255,.8);text-align:left;position:absolute;font-weight:100;font-size:12px;">{{`来自 ${event.author_token} 的回答「${event.related_question_title}」`}}</div>
            </div>
        </swiper-slide>
    </swiper>
    <div
        class='page-item page-1'        
        :style="{opacity:index == 1 ? 1 : 0,...zIndex(1)}">
        <div 
            v-if='showQrcode'
            style="position:absolute;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.8);z-index:1">
            <div
                @click='showQrcode = false'
                :style='{top:tpx(486,1800),right:px(120)}'
                style="position:absolute;width:50px;height:50px;z-index:1"></div>
            <img 
                src='../assets/qrcode.png'
                :style='{top:tpx(478,1800),width:px(524),height:px(750),left:px(118)}'
                style="position:absolute;" />
        </div>
        <div
            class='page-1-btn'
            v-if='loaded'
            @click="next"
            :class='show[1] ? "page-1-btn-show" : "page-1-btn-hide"'
            :style="{height:px(93),bottom:px(66),width:px(410),left:px(172)}">
            <img
                style="width:100%;"
                src='../assets/1_1.png' />
        </div>
    </div>
    <div
        class='page-item page-2'
        :style="{opacity:index == 2 ? 1 : 0,...zIndex(2)}">
        <div
            :style='{top:tpx(364,1800)}'
            style="position:absolute;width:100%;text-align:center;color:#2570EF">
            <span>已备忘</span>
            <span :style="{marginLeft:px(20)}">{{selectEvents.length}} / {{total}}</span>
        </div>
        <div
            :style="{
                left:px(11),
                top:tpx(450,1800),
                width:px(94),
                height:px(830)}"
            class='months'>
            <div
                @click="selectMonth(index)"
                :style="{height:px(56),lineHeight:px(62),marginTop:px(13.5),borderRadius:px(12)}"
                :id='"months"+index'
                :class='[selectMonthIndex == index && "months-active","months-"+index]'
                :key='"months" + index'
                v-for='(item,index) in _range(12)'>
                <span v-if='selectMonthIndex == index'>{{index + 1}} 月</span>
            </div>
        </div>
        <div 
            ref='events'
            :style="{
                top:tpx(450,1800),
                height:px(856),
                width:px(545),
                left:px(120 - 19)}" 
            class='events'>
            <template
                v-for='(item,index) in eventList'>
                <EventItem
                    :key='`events-${index}`'
                    :class='"events-"+index'
                    :selected='selectKeys.indexOf(index) > -1'
                    :item='item'
                    @select="selectEvent(index,item)"
                    @detail='showDetail(index)'
                    :eventItem='eventItem'
                    :isShowCheck="true" />
            </template>
        </div>
        <div
            v-if='loaded && selectKeys.length'
            @click="next"
            :style="{height:px(93),top:tpx(1390,1800)}"
            style="position:absolute;width:100%;cursor:pointer;"></div>
    </div>
    <div
        class='page-item page-3'
        :style="{opacity:index == 3 ? 1 : 0,...zIndex(3)}">
        <div
            v-show='isLoadingCapture'
            :style="{height:innerHeight+'px'}"
            style="position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.6);z-index:100;transition:all 1s;">
            <div style="position:absolute;top:50%;left:50;color:#FFF;font-size:14px;text-align:center;width:100%;margin-top:-20px;">加载中..</div>
        </div>
        <Capture 
            @ok='isLoadingCapture = false'
            @back='index = 2'
            v-if='index == 3'
            :eventItem='eventItem'
            :selectEvents='selectEvents'></Capture>
    </div>
</div>
</template>

<script>
import base from '../base'
import axios from 'axios'
import _sortby from 'lodash.sortby'
// import _clone from 'lodash.clonedeep'
import _debounce from 'lodash.debounce'
import tween from '@tweenjs/tween.js'
import { isWeixin, isZhihu } from '../util'
import EventItem from './event.vue'
import Capture from './capture.vue'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
import fetchData from "../utils/bundle.min"
import "../utils/zsencrypt"

export default {
    mixins:[base],
    components:{
        EventItem,
        Capture,
        Swiper,
        SwiperSlide,
    },
    data(){
        return {
            index:0,
            events:[],
            eventList:[],
            total:0,
            selectKeys:[],
            selectEvents:[],
            selectMonthIndex:0,
            isShowQrcode:!isWeixin() && !isZhihu(),
            showQrcode:false,            
            eventItem:{
                size:"",
                radius:"",
                left:"",
                top:"",
            },
            isLoadingCapture:false,
            swiperOption: {
                slidesPerView: 'auto',
                centeredSlides: true,
                spaceBetween: 30,
                pagination: {
                    el: '.swiper-pagination',
                    clickable: true
                }
            },
            isViewEvents:false,
            show:{
                1:false,
                detail:false,
            },
            me:{
                name:"null",
                avatar_url:"https://pic4.zhimg.com/cf3e8efc5571026532415b891a9a29d7_xll.jpg",
            }
        }
    },
    watch:{
        "$main.percent"(a){
            if(a == 100){
                this.index = 1
            }
        },
        index(a){
            if(a == 1){
                setTimeout(() => this.show[1] = true,200)
            }
            if(a == 3){
                this.isLoadingCapture = true
            }
        }
    },
    computed:{
        loaded(){
            return this.$main.percent == 100
        },
        eventItemHeight(){
            return parseFloat(this.eventItem.size) + parseFloat(this.eventItem.top) + parseFloat(this.px(8))
        },
    },
    async created(){        
        this.eventItem.size = this.px(160)
        this.eventItem.radius = this.px(10)
        this.eventItem.left = this.px(19)
        this.eventItem.top = this.px(12)
        //----------------------------
        // 微信端获取个人信息
        //----------------------------
        if(isWeixin()){
            const me = (location.search.split("?")[1] || "").split("&").reduce((res,item) => {
                const [ key, value ] = item.split("=")
                res = res || {}
                res[key] = decodeURIComponent(value)
                return res
            },{})
            if(me.nickname){
                this.me.name = me.nickname
                this.me.avatar_url = me.avatar
            }else{
                location = `https://www.zhihu.com/oauth/redirect/social_info/billboardseventy?from=${location.href}`
            }
        }
        //----------------------------
        // 知乎端获取个人信息
        //----------------------------
        if(isZhihu()){
            try{
                const me = await fetchData("https://www.zhihu.com/api/v4/me");
                if(me && me.data){
                    this.me = me.data
                }
            }catch(e){
                console.warn(e)
            }
        }
    },
    async mounted(){
        let events = await axios.get("https://www.zhihu.com/events/2020")
        events = (events && events.data && events.data.data || []).map(item => {
            item.event_day = parseInt(item.event_day)
            item.event_month = parseInt(item.event_month)
            return item
        })
        // events = events.concat(_clone(events)).concat(_clone(events)).concat(_clone(events))
        this.total = events.length
        events = _sortby(events,['event_month','event_day'])
        events = events.map((item,index) => {
            item.index = index
            return item
        })
        this.eventList = events
        this.events = events.reduce((res,item) => {
            res = res || {}
            res[item.event_month] = res[item.event_month] || {}
            res[item.event_month].month = item.event_month
            res[item.event_month].events = res[item.event_month].events || []
            res[item.event_month].events.push(item)
            return res
        },{})
        this.events = Object.values(this.events)
        this.$refs.events.addEventListener("scroll",_debounce(this.eventScroll,100))
    },
    methods:{
        next(){
            if(this.isShowQrcode){
                this.showQrcode = true
                return;
            }
            this.index += 1
        },
        log(a){
            console.log(JSON.stringify(a,null,4))
        },
        zIndex(a){
            return {
                zIndex:this.index == a ? 2 : 1
            }
        },
        selectEvent(key,item,isDetail){
            const index = this.selectKeys.indexOf(key)
            if(index > -1){
                if(!isDetail){
                    this.selectKeys.splice(index,1)
                    this.selectEvents.splice(index,1)
                }
            }else{
                this.selectKeys.push(key)
                this.selectEvents.push(item)
            }
        },
        eventScroll(e){
            if(this.lockScroll){
                return;
            }
            const { scrollTop } = e.target
            const row = parseInt(scrollTop / this.eventItemHeight)
            const start = row * 3
            const month = this.eventList.slice(start,start + 3).pop()
            this.selectMonthIndex = month.event_month - 1
        },
        selectMonth(index){
            this.selectMonthIndex = index
            const event = this.eventList.filter(item => item.event_month >= index + 1)[0]
            const eIndex = event && event.index
            if(!(eIndex >= 0)){
                return;
            }
            const row = parseInt(eIndex / 3)
            this.scrollTo(row * this.eventItemHeight)
            this.lockScroll = true
            setTimeout(() => this.lockScroll = false,500)
        },
        scrollTo(scrollTop){
            const _s = this
            const start = this.$refs.events.scrollTop, end = scrollTop
            function animate () {
                if (tween.update()) {
                    requestAnimationFrame(animate)
                }
            }
            const data = { scrollTop: start }
            new tween
                .Tween(data)
                .to({ scrollTop: end }, 300)
                .onUpdate(function () {
                    _s.$refs.events.scrollTop = data.scrollTop
                })
                .start()
            animate()
        },
        showDetail(key){
            this.show.detail = true
            if(this.$refs && this.$refs.details){
                this.$refs.details.$swiper.slideTo(key, 0)
                return;
            }
            setTimeout(() => this.showDetail(key),50)
        }
    }
}
</script>

<style lang='scss'>
.page {
    &-item{
        opacity: 1;
        transition: all .3s;
        position: absolute;
        width: 100%;
        height: 100%;
        top:0;
        left:0;
    }
    &-1{
        background: url(../assets/1.jpg);
        background-size: 100%;
        background-position: center;
        &-btn{
            position:absolute;
            transition: all .5s;
            &-hide{
                transform:translateY(100px);
                opacity: 0;
            }
            &-show{
                transform:translateY(0px);
                opacity: 1;
            }
        }
    }
    &-2{
        background: url(../assets/2.jpg);
        background-size: 100%;
        background-position: center;
    }
    &-3{
        position: relative;
    }
}
.events{
    position:absolute;
    overflow-y: auto;
    transition: all .5s;
    &-item{
        display: inline-block;
        background: #FFF;
        overflow: hidden;
        cursor: pointer;
        box-shadow: 1px 2px 3px 1px #0346B7;
        &-time{
            display: flex;
            align-items: center;
            justify-content:space-between;
            &-check{
                border-radius: 50%;
                background: #FFF;
                position: relative;
                >div{
                    border: #CCC 2px solid;
                    border-top: 0;
                    border-right: 0;
                    transform: rotate(-45deg);
                    position: absolute;    
                }
            }
        }
        &-name{            
            display: flex;
            align-items: center;
            text-align: center;
            justify-content:center;
            color:#666;
            line-height: 1.2;
            box-sizing: border-box;
            background: #FFF;
            overflow: hidden;
        }
    }
    &-1,
    &-2,
    &-0{
        margin-top:0!important;
    }
    &-detail{
        background: rgba(0,0,0,.6);
        position:absolute;
        z-index:10;
        .swiper-slide{
            background: rgb(147, 215, 178);
            >div{
                position: relative;
                width:100%;
                height:100%;
                background: url(../assets/2_1.png);
                background-size: 100%;
                background-position: center;
            }
        }
    }
}
.months{
    position: absolute;
    >div{
        color:#FFF;
        font-size:12px;
        text-align: center;
        overflow: hidden;
    }
    &-0{
        margin-top:0!important;
    }
    &-active{
        background:url(../assets/btn1.png);
        background-size: 100%;
        font-weight: bold;
    }
}
.swiper {
    height: 100%;
    width: 100%;
    .swiper-slide {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        font-weight: bold;
    }
}
</style>