<template>
<div class='capture'>
    <template
        v-if='!capture'>
        <img src='../assets/4_1.jpg' style="width:100%;display:block;" />
        <img
            crossOrigin="anonymous"
            ref='headimg'
            @load='headimgOnload'
            :style='{top:px(66),left:px(25),width:px(132),height:px(132)}'
            style="position:absolute;border-radius:50%;overflow: hidden;z-index:1"
            :src='headimgData || $parent.me.avatar_url' />
        <div
            :style="{top:px(74),left:px(164),width:px(240),height:px(62),lineHeight:px(62)}"
            style="position:absolute;
                overflow: hidden;
                background:#1380F9;
                color:#FFF;
                text-align:center;">{{$parent.me.name}}</div>
        <div
            :style="{bottom:px(156),height:px(58),left:px(56)}"
            style="position:absolute;
                color:#FFF;
                text-align:center;
                font-size:12px;">
            2020年，我记下了{{selectEvents.length}}件大事件<br />
            falsdjflasdjf;lasdfjasld;fjas;ld
        </div>
        <div 
            class='capture-list'
            :style="{
                minHeight:innerHeight / 2 + 'px',
                height:getListHeight()+'px'}"></div>
        <div
            style="position:relative;z-index:1;"
            :style='{
                minHeight:innerHeight / 2 + "px",
                marginTop:px(-220),width:px(600),paddingLeft:px(74)}'>
            <div
                style="display:inline-block;text-align:left;">
                <EventItem
                    style="background:inherit!important"
                    :key='`events-${index1}`'
                    v-for='(event,index1) in selectEvents'
                    :item='event'
                    :eventItem='eventItem'
                    :itemSize='parseFloat(eventItem.size) + parseFloat(px(11)) + "px"'
                    class='events-item' />
            </div>
        </div>
        <img 
            :style="{marginTop:px(-256)}"
            src='../assets/4_3.jpg' style="width:100%;z-index:1;display:block;" />
    </template>
    <template v-else>            
        <img 
            style="width:100%;display:block;"
            :src='capture' />
        <div
            :style="{height:px(278)}"></div>
        <div
            :style="{height:px(278)}"
            class='capture-save'>
        <!--查看更多按钮-->
        <div
            @click="back"
            class='btn'
            :style="{bottom:px(53),width:px(214),height:px(90),right:px(118)}"
            style="position:absolute;cursor:pointer;"></div>
        </div>
    </template>
</div>
</template>

<script>
import html2canvas from 'html2canvas'
import base from '../base'
import EventItem from './event.vue'
import { getBase64Image } from '../util'

export default {
    mixins:[base],
    components:{
        EventItem,
    },
    props:{
        eventItem:Object,
        selectEvents:Array,
    },
    data(){
        return {
            capture:"",
            headimgData:"",
        }
    },
    mounted(){
        document.body.style.overflow = 'auto';
    },
    methods:{
        getListHeight(){
            const itemHeight = parseFloat(this.eventItem.size) + parseFloat(this.px(11))
            return Math.ceil(this.selectEvents.length / 3) * itemHeight;
        },
        back(){
            this.capture = null
            this.$emit("back")
        },
        headimgOnload(){
            const img = document.createElement('img'); 
            img.src = this.$parent.me.avatar_url
            img.setAttribute("crossOrigin",'anonymous')
            img.onload = () =>  { 
                try{
                    const headimgData = getBase64Image(img);
                    this.headimgData = headimgData
                }catch(e){
                    alert(e)
                }
                setTimeout(() => {
                    html2canvas(this.$el).then(canvas => {
                        this.capture = canvas.toDataURL("image/jpeg",1)
                        this.$emit("ok")
                    });
                },2000)
            }
        }
    }
}
</script>

<style lang='scss'>
.capture{
    &-list{
        z-index: -1;
        position:absolute;
        background:url(../assets/4_2.jpg);
        background-size: 100%;
        width:100%;
        margin-top:-2px;
    }
    &-save{
        width:100%;
        position:fixed;
        bottom:0;
        left:0;
        background:url(../assets/4_4.jpg);
        background-size: 100%;
    }
}
</style>