<template>
<div
    v-if='show'
    :style="{opacity:$main.percent == 100 ? 0 : 1}"
    class='loading'>
    <div 
        :style='{
            borderRadius:px(20),
            height:px(32),
            top:tpx(852.5,1331),
            width:px(485 * ($main.percent / 100)),
            left:px(132),
        }'
        class='loading-percent'></div>
</div>
</template>

<script>
import base from '../base'
import { getRangeRandom } from '../util'

export default {
    mixins:[base],
    data(){
        return {
            show:true,
        }
    },
    watch:{
        "$main.percent"(a){
            if(a == 100){
                setTimeout(() => this.show = false,1000)
            }
        }
    },
    mounted(){
        const fn = (num) => {
            num = num || 0
            if(num == 4){
                this.$main.percent = 100
                return;
            }
            this.$main.percent = getRangeRandom(num * 20,(num + 1) * 20)
            setTimeout(() =>{
                fn(num + 1)
            },500)
        }
        fn()
    },

}
</script>

<style lang='scss'>
.loading{
    opacity: 1;
    transition: all .3s;
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    background: url(../assets/loading-bg.jpg);
    background-size: 100%;
    background-position: center;
    &-percent{
        background:#FDD679;
        position: absolute;
        transition: all .3s;
    }
}
</style>